.ToDoItem {
  background: rgb(250,250,250);
  -webkit-box-shadow: .1rem .1rem 2rem rgba(51,51,51,0.4);
          box-shadow: .1rem .1rem 2rem rgba(51,51,51,0.4);
  border: 0;
  position: relative;
  height: 20rem;
  overflow: auto;
  width: 20rem;
  margin: 1rem;
  padding: .25rem 1rem;
  overflow-x: hidden;
  overflow-y: auto;
  word-wrap: break-word;
}

.ToDoItem h1 {
  font-size: 1.5rem;
  font-weight: 500;
  color: rgb(40, 40, 40);
}

.ToDoItem button {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  background: none;
  color: rgb(100, 100, 100);
  border: 0;
  padding: .25rem 1rem;
  font-size: 1.7rem;
  font-weight: 700;
  position: absolute;
  top: 0.75rem;
  right: 0rem;
  width: auto;
}

.ToDoItem button:hover {
  cursor: pointer;
  color: rgb(40, 40, 40);
}



