.ToDoForm {
  display: block;
}
.ToDoForm 

input[type="text"] {
  font-size: 1.1rem;
  margin: .5rem;
  padding: .3rem;
  height: 100%;
  background: rgb(250,250,250);
  color: rgb(40, 40, 40);
}

.ToDoForm > button {
  text-transform: uppercase;
  -webkit-box-shadow: .1rem .1rem 2rem rgba(250,250,250,0.2);
          box-shadow: .1rem .1rem 2rem rgba(250,250,250,0.2);
  border: 0;
  font-weight: 500;
  font-size: 1.1rem;
  padding: .3rem;
  width: 4rem;
  background-color: rgb(40, 40, 40);
  color: rgb(250,250,250);
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}
.ToDoForm > button:hover {
  cursor: pointer;
  background-color: rgb(250,250,250);
  -webkit-box-shadow: .2rem .2rem 1rem rgba(51,51,51,0.2);
          box-shadow: .2rem .2rem 1rem rgba(51,51,51,0.2);
  color: rgb(40, 40, 40);
}



